* {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

body {
  overflow: hidden;
}

td {
  padding: 0 3px;
}

.container {
  height: 94vh;
  overflow-y: auto;
}

.container-inner {
  padding: 10px;
}

.pointer:hover {
  cursor: pointer;
  text-decoration: underline;
}

.pointer-no-u:hover {
  cursor: pointer;
  text-decoration: none;
}

.hovered:hover {
  background-color:rgba(0, 0, 0, 0.15);
}

header {
  height: 60px;
  background-color: rgb(32, 87, 158);
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.navbar {
  background-color: rgb(32, 87, 158);
  height: 100vh;
}

.navbar-top p {
  padding-left: 60px;
  padding-right: 60px;
}

.navbar-top {
  display: none;
}

.dropdown {
  display: none;
}

.sign-in {
  margin-top: 15%;
}

.select-week {
  display: flex;
  align-items: center;
  text-align: center;
}

.select-week .relative .absolute {
  top: 0;
  width: 85%;
  height: 100%;
}

#manage-presets .absolute {
  top: 0;
  opacity: 0;
}

#manage-presets .absolute:hover {
  opacity: 100%;
}

.schedules-controller {
  margin-bottom: 10px;
  border: solid 1px rgb(177, 177, 177);
  display: flex;
  justify-content: space-around;
  padding: 5px;
}

#controller-date {
  margin-left: 15px;
  margin-right: 15px;
}

.schedules-requests {
  display: flex;
  align-items: center;
}

.schedules-text {
  font-size: calc(5px + 0.8vmin);
}

.schedules-table th,td {
  border: solid 1px rgb(177, 177, 177);
}

.schedules-mobile {
  display: none;
}

.availability {
  margin-top: 10px;
}

.availability table th,td {
  border: solid 1px rgb(177, 177, 177);
}

.table-fixed {
  table-layout: fixed;
}

